import Vue from 'vue'
import router from '../router'
import message from '../components/Message.vue'
import store from '../store'

export default {
    domain: '',
    apiBase: '',
    codeMessage: {
        1: '',
        2: '',
        3: '',
        4: '',
        8: '',
        9: '',
        11: '',
        12: '',
        13: '',
        90: 'IO',
        99: '',
        600: '',
        605: '',
        1000: '',
        1001: '',
        1002: '',
        1003: 'email',
        1004: 'email',
        1005: '',
        1006: '',
        1007: '',
        1008: '',
        1009: '',
        1100: '',
        1101: '',
        1102: '',
        1500: '',
        5001: 'license',
        5002: 'license',
        5003: '',
        5004: 'pubkey',
        5005: 'license',
        5006: 'XML',
        5007: 'XML',
        5008: 'IO',
        5009: '',
        5010: '',
        5011: '',
        5012: '',
        5013: 'license',
        51101: '',
        51003: '',
        51104: 'json',
        51105: '',
        51115: 'arrayitem,',
        6201: 'iwa',
        6202: 'iwa',
        6203: '',
        6204: 'iwa',
        6118: '',
        6205: 'iwa',
        6206: '6206',
        6207: 'iwa',
        6210: 'iwa',
        6211: 'iw',
        6212: '',
        6213: 'pubkeu',
        6214: '',
        6215: 'projectID',
        default: ''
    },
    showErrorAlert (code) {
        if (!code) {
            Vue.prototype.$message.alert(this.codeMessage.default);
        } else if (code === 3 || code === 4) {
            router.push('/')
            Vue.prototype.$message.alert('');
            store.dispatch('clearToken')
            store.dispatch('clearApplications')
        } else {
            if (this.codeMessage[code]) {
                Vue.prototype.$message.alert(this.codeMessage[code]);
            } else {
                Vue.prototype.$message.alert(this.codeMessage.default);
            }

        }
    },
    parseDateTime (timestamp) {
        let d = new Date(timestamp);
        return d.getFullYear() + '-' + this.serializeNum(d.getMonth() + 1) + '-' + this.serializeNum(d.getDate())
            + ' ' + this.serializeNum(d.getHours()) + ':' + this.serializeNum(d.getMinutes()) + ':' + this.serializeNum(d.getSeconds());
    },
    parseDate (timestamp) {
        let d = new Date(timestamp);
        return d.getFullYear() + '-' + this.serializeNum(d.getMonth() + 1) + '-' + this.serializeNum(d.getDate());

    },
    serializeNum (num) {
        return num > 9 ? num : '0' + num;
    },
    dataTableChinese (name) {
        return {
            bLengthChange: false,
            bFilter: false,
            bInfo: false,
            bSort: false,
            sPaginationType: "full_numbers",
            bRetrieve: true,
            iDisplayLength: 10,
            oLanguage: {
                sLengthMenu: " _MENU_ ",
                sInfo: " _START_  _END_ / _TOTAL_ ",
                sInfoEmpty: "",
                sInfoFiltered: "( _MAX_ )",
                sZeroRecords: "",
                sSearch: name,
                oPaginate: {
                    sFirst: "",
                    sPrevious: '',
                    sNext: "",
                    sLast: ""
                }
            }
        }
    }
}



